import { useState, useEffect } from 'react'
import useStats from "../src/useStats";
import Stats from "../components/Stats";
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/core/styles';
import theme from '../src/theme';
import Grid from '@material-ui/core/Grid';
import Select from '@material-ui/core/Select';
import NativeSelect from '@material-ui/core/NativeSelect';
import InputBase from '@material-ui/core/InputBase';



var emojiFlags = require('emoji-flags');


const BootstrapInput = withStyles(theme => ({
    root: {
      'label + &': {
        marginTop: theme.spacing(3),
        position:'inherit',
        margin: '0px auto'
      },
    },
    input: {
      margin: `${theme.spacing(3)}px auto`,
      width: '100%',
      display: 'block',
      borderRadius: 4,
      backgroundColor: theme.palette.background.paper,
      border: '1px solid #ced4da',
      fontSize: 16,
      padding: '10px 26px 10px 12px',
      transition: theme.transitions.create(['border-color', 'box-shadow']),
      // Use the system font instead of the default Roboto font.
      fontFamily: [
        '-apple-system',
        'BlinkMacSystemFont',
        '"Segoe UI"',
        'Roboto',
        '"Helvetica Neue"',
        'Arial',
        'sans-serif',
        '"Apple Color Emoji"',
        '"Segoe UI Emoji"',
        '"Segoe UI Symbol"',
      ].join(','),
      '&:focus': {
        borderRadius: 4,
        borderColor: '#80bdff',
        boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
      },
    },
  }))(InputBase);
  
  const useStyles = makeStyles(theme => ({
    margin: {
      margin: theme.spacing(1),
    },
  }));

export default function CountrySelector() {
    const classes = useStyles();
    const {stats: countries, loading, error} = useStats('https://covid19.mathdro.id/api/countries');
    const [selectedCountry, setSelectedCountry] = useState('MX');
    const [emoji,setEmoji]  = emojiFlags.countryCode(selectedCountry);

    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error...</p>;
    
    return (
        <Grid item xs={12}>
            {emoji}
            <NativeSelect  id="demo-customized-select-native"  input={<BootstrapInput />} onChange={(e) => {setSelectedCountry(e.target.value), setEmoji(e.target.value) }}>
                    {Object.entries(countries.countries).map(([country, code]) => {
                        return (
                            <option selected={selectedCountry === code} key={code} value={countries.iso3[code]}>
                               {countries.iso3[code]} - {country}
                            </option>
                        )
                    })}
            </NativeSelect >
            <Stats url={`https://covid19.mathdro.id/api/countries/${selectedCountry}`}> </Stats>
        </Grid>
    );
}